<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process120"></a>

<p><br></p>
<p><span class="rvts17">支线：暴君套装（</span><span class="rvts50">Artefacts of Tyrant</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">上篇</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span><span class="rvts20">X:397,Y:552</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：暴君雕像</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标</span><span class="rvts26">：</span>
<br><span class="rvts21">取得暴君战袍</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1035.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">暴君套在本章节逃出欢乐堡之前，只能获得一件。然而这一件也是期初最难拿的一件，因为它需要</span><span class="rvts20">14-16</span><span class="rvts21">点的智谋值（</span><span class="rvts20">Wit</span><span class="rvts21">）。</span>
<br><span class="rvts21">由于该属性不属于主属性，因此前期只能通过加点或装备加成（前期似乎并没有增加智谋值的装备），或使用心灵平静（</span><span class="rvts20">Peace of Mind</span><span class="rvts21">）技能，进行提升。</span>
<br><span class="rvts21">之后的暴君套装，笔者将在下一章节列出它们的详细位置。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



